.bg-primary {
  background-color: #317538; }

.bg-primary-0 {
  background-color: #317538; }

.bg-primary-light-1 {
  background-color: #435c4f; }

.bg-primary-light-2 {
  background-color: #8e9e96; }

.bg-primary-dark-1 {
  background-color: #062114; }

.bg-primary-dark-2 {
  background-color: #000503; }

.rotate {
  animation: rotate 0.5s ease-in-out; }

.rotate-cc {
  animation: rotate_cc 0.5s ease-in-out; }

.text-primary {
  color: #062114 !important; }

[data-toggle="dropdown"] {
  cursor: pointer; }

.edito a[href]:not([href="#"]),
#glossary-term a[href]:not([href="#"]) {
  font-style: italic;
  font-weight: 500; }

.btn.see-all:hover {
  color: white;
  background-color: #317538; }

.lh-10 {
  line-height: 1.0; }

.lh-11 {
  line-height: 1.1; }

.lh-12 {
  line-height: 1.2; }

.lh-13 {
  line-height: 1.3; }

.lh-14 {
  line-height: 1.4; }

.lh-15 {
  line-height: 1.5; }

.position-left {
  left: 0 !important;
  right: auto !important; }

.position-right {
  left: auto !important;
  right: 0 !important; }

.position-top {
  top: 0 !important;
  bottom: auto !important; }

.position-bottom {
  top: auto !important;
  bottom: 0 !important; }

.spacer {
  padding: 1em 0; }
  .spacer.double {
    padding: 2em 0; }
  .spacer hr {
    margin: 0;
    padding: 0; }

.modal-auto .modal-dialog {
  max-width: 20em;
  transition: max-width .5s ease-in, transform .3s ease-out !important; }

.thumbnail img {
  width: auto !important;
  max-width: 150px;
  height: auto !important;
  max-height: 150px; }

.icon {
  width: 80px;
  height: 80px;
  flex-shrink: 0; }

.svg-container img {
  width: 100%;
  height: 100%; }

.svg-container svg {
  max-width: 100%;
  max-height: 100%; }

.filter-grayscale {
  filter: grayscale(); }

.break-avoid {
  break-inside: avoid; }

.wrapper-quote {
  position: relative;
  padding-left: 80px; }
  .wrapper-quote::before {
    content: "«";
    display: block;
    position: absolute;
    left: 20px;
    font-size: 4em;
    font-style: italic;
    line-height: 0.5; }
  .wrapper-quote .quote {
    font-style: italic; }

.wrapper-image img {
  width: 100%; }

.wrapper-imagemap {
  position: relative;
  display: inline-flex;
  width: 100%; }
  .wrapper-imagemap img {
    width: 100%;
    height: 100%; }
  .wrapper-imagemap svg {
    position: absolute;
    left: 0;
    right: 0;
    fill: #888;
    fill-opacity: 0;
    width: 100%;
    height: 100%; }

.wrapper-nav-tabs a:hover {
  color: #e9ecef; }

.tags a {
  font-weight: 500 !important;
  font-style: normal !important; }

.tags a:hover .badge {
  color: white !important;
  background-color: #317538 !important; }

.thesaurus-term {
  cursor: pointer;
  font-style: italic;
  font-weight: 500;
  color: #435c4f; }

/**
 * Calendar styles
 */
.fc-daygrid .fc-col-header-cell {
  background-color: #062114;
  color: white; }

.fc-daygrid .fc-day .fc-event {
  cursor: pointer; }

.leaflet-control-layers .leaflet-control-layers-list {
  border: none !important; }

